import VHeader from '@/components/v-header/v-header'
import dynamicList from '@/pages/meiTu/dynamicList/dynamicList'

import { isIphoneX } from '@/assets/js/brower'
import { localUser } from '@/assets/js/local'
import axios from '@/api/axiosApi'
import { personifyTime } from '@/assets/js/utils'
import defaultAvatar from '@/assets/img/default_avatar.png'
import { QINIU_PREFIX } from '@/api/config'

export default {
  name: 'UserDynamic',
  data () {
    return {
      showHeaderBackground: false,
      followStatus: 0,
      user: {},
      dynamicList: []
    }
  },
  components: {
    VHeader, dynamicList
  },
  created () {
    this.paddingTop = 40
    if (isIphoneX()) {
      this.paddingTop += 34
    } else {
      this.paddingTop += 20
    }
    this.pageNum = 0
    this._getUserInfo()
    this._getUserDynamic(0)
  },
  methods: {
    _getUserInfo () {
      axios.get(this, `/v1/userInfo/${this.$route.params.userId}`, null, (data) => {
        if (data.headUrl) {
          if (data.headUrl.indexOf(QINIU_PREFIX) > -1) {
            data.headUrl += '-bheader'
          }
        } else {
          data.headUrl = defaultAvatar
        }
        this.user = data
      })
    },
    _getUserDynamic (pageNum) {
      const _self = this
      axios.get(_self, '/v1/dynamic', {
        publisher: this.$route.params.userId,
        curUserId: localUser.get('id'),
        pageNum: pageNum
      }, (data) => {
        if (data.length === 0) {
          if (pageNum > 0) {
            if (_self.$refs.dynamicList) {
              _self.$refs.dynamicList.$refs.scroll.hiddenPullUpLoad()
            }
          } else {
            _self.pageNum = 0
            _self.$refs.dynamicList.$refs.scroll.hiddenPullDown()
          }
          return
        }
        this.pageNum++
        let temp = []
        data.forEach(item => {
          let tmpHeadUrl = defaultAvatar
          if (item.headUrl) {
            if (item.headUrl.indexOf(QINIU_PREFIX) > -1) {
              tmpHeadUrl = item.headUrl + '-bheader'
            } else {
              tmpHeadUrl = item.headUrl
            }
          }
          _self.followStatus = item.follower === 'NO' ? 0 : 1
          temp.push({
            user: {
              id: item.publisher,
              nickname: item.nickname,
              headUrl: tmpHeadUrl,
              signature: item.signature || '',
              local: parseInt(item.local)
            },
            product: {
              id: item.productId,
              thumblUrl: item.thumbUrl,
              name: item.name,
              address: item.address,
              type: item.type
            },
            dynamic: {
              id: item.id,
              content: item.content,
              atlas: item.atlas ? item.atlas.split(';') : '',
              publishAddress: item.publishAddress,
              publishTime: personifyTime(item.publishTime),
              tags: item.tagNames ? item.tagNames.split(';') : '',
              tagIds: item.tagIds ? item.tagIds.split(';') : '',
              latitude: item.publishLatitude,
              longitude: item.publishLongitude
            },
            other: {
              collectionType: item.collection !== 0,
              starType: item.star !== 0,
              collectionNum: item.collectionSum,
              starNum: item.starSum,
              // shareNum: item.shareCount,
              commentNum: item.commentCount,
              followStatus: item.follower === 'NO' ? 0 : 1
            }
          })
        })
        _self.dynamicList = temp
      })
    },
    scroll (position, directionY) {
      if (position.y <= (this.$refs.head.$el.clientHeight - this.$refs.top.clientHeight)) {
        this.showHeaderBackground = true
      } else {
        this.showHeaderBackground = false
      }
    },
    changeFollowStatus (targetUserId) {
      const userId = localUser.get('id')
      if (userId == null) {
        this.$vux.toast.text('请先登录！', 'middle')
        return
      }
      if (this.sendRequest) {
        return
      }
      if (userId === targetUserId) {
        this.$vux.toast.text('自己不能关注自己！', 'middle')
        return
      }
      const valid = this.followStatus === 0 ? 1 : 0
      this.followStatus = valid
      this.sendRequest = true
      const _self = this
      axios.post(this, `/v1/follow/${userId}/${targetUserId}/${valid}`, null, (data) => {
        _self.sendRequest = false
      }, null, false)
    },
    sendMessage () {
      if (localUser.get('id') == null) {
        this.$vux.toast.text('请先登录！', 'middle')
        return
      }
      this.$store.dispatch('setChatObject', this.user)
      this.$router.push('/chat')
    },
    toFollow () {
      if (localUser.get('id') == null) {
        this.$vux.toast.text('请先登录！', 'middle')
        return
      }
      this.$router.push('/user/focus/' + this.$route.params.userId)
    },
    toFans () {
      if (localUser.get('id') == null) {
        this.$vux.toast.text('请先登录！', 'middle')
        return
      }
      this.$router.push('/user/fans/' + this.$route.params.userId)
    },
    onPullingUp() {
      this._getUserDynamic(this.pageNum + 1)
    },
    onPullingDown() {
      this._getUserDynamic(0)
    }
  }
}



